import './style.scss';

const FilterSection = () => {
    return (
        <div className='filter-section'>
            <div className='filter-category-wrapper'>
                <img src={require('@svgs/envelope.svg').default} />
                <img src={require('@svgs/file.svg').default} />
                <img src={require('@svgs/presentation.svg').default} />
                <img src={require('@svgs/fileaudio.svg').default} />
                <img src={require('@svgs/filevideo.svg').default} />
                <img src={require('@svgs/message.svg').default} />
            </div>
            <div className='filter-body'>
                <div className='filter-body-row1'>
                    <span>Filter Set 1</span>
                    <hr className='divider'/>
                </div>
                <span className='label'>And</span>
                <span className='filter-item'>Responsive -Assist <span  className='value'>{'>90%'}</span></span>
                <span className='label'>Not</span>
                <span className='filter-item red'>Reviewed</span>
                <span className='label'>And</span>
                <span className='filter-item'>Assigned to Me</span>
            </div>
            <div>
                <hr className='divider'/>
            </div>
            <div className='filter-category-wrapper'>
                <img src={require('@svgs/edit.svg').default} />
                <img src={require('@svgs/folder.svg').default} />
                <img src={require('@svgs/filter.svg').default} />
                <img src={require('@svgs/save.svg').default} />
            </div>
        </div>
    )
}
export default FilterSection;