import { Select } from 'antd';
import './style.scss';

const Information = () => {
    return (
        <div className='dashboard-info'>
            <div className='dashboard-info-header'>
                <div className='dashboard-info-header-filter'>
                    <Select 
                        className='case-select'
                        style={{width: '160px', marginBottom:0}}
                        labelInValue
                        defaultValue={{
                            label: "Last Updated",
                            value: '0',
                        }}
                        onChange={() => {}}
                        options={[{
                            label: "Last Updated",
                            value: '0',
                        }]}
                        bordered={false}
                    />
                    <Select 
                        className='case-select'
                        style={{width: '160px', marginBottom:0}}
                        labelInValue
                        defaultValue={{
                            label: "Sort Field 2",
                            value: '0',
                        }}
                        onChange={() => {}}
                        options={[{
                            label: "Sort Field 2",
                            value: '0',
                        }]}
                        bordered={false}
                    />
                    <a href='#' style={{height:'32px'}}>
                        <img src={require('@svgs/plus.svg').default} />    
                    </a>
                </div>
                <div className='dashboard-info-header-viewmode' style={{float:'right'}}>
                    <img src={require('@svgs/row3.svg').default} /> 
                    <img src={require('@svgs/row2.svg').default} /> 
                    <img src={require('@svgs/row1.svg').default} /> 
                </div>
            </div>
            <div className='dashboard-info-body'>
                <span className='label'>Source ID: <span className='value'>AH6632KJ</span>
                    <a href='#' className='source-id-link'>E</a><a href='#' className='source-id-link'>Source Link</a>
                    <span className='label' style={{float:'right', marginRight:'20px'}}>Last Updated: 2023-01-07</span>
                </span>
                <span className='label'>From: <span className='value'>jackgreenield@gmail.com</span></span>
                <span className='label'>To: <span className='value'>jackgreenield@gmail.com, madnessmax@gmail.com</span></span>
                <span className='label'>CC: <span className='value'>jackgreenield@gmail.com</span><span className='label'>CC: <span className='value'>jackgreenield@gmail.com</span></span></span>
                <span className='label'>Subject: <span className='value black'>Re: Can we setup some time to talk maybe Monday or Tuesday next week?</span></span>
                <span className='label'>Reply? <span className='value'>AH6632KJ</span><span className='label'>Forward? <span className='value black'>No</span></span></span>
                <span className='summary'>No summary available</span>
                <a href='#' className='button'>Summarize</a>
            </div>
            
            <div>
                <hr className='divider'/>
            </div>
            <span className='label'>Source ID: <span className='value'>AH6632KJ</span>
                <a href='#' className='source-id-link red'>P</a><a href='#' className='source-id-link'>Source Link</a>
                <span className='label' style={{float:'right', marginRight:'20px'}}>Last Updated: 2023-01-07</span>
            </span>
        </div>
    )
}
export default Information;