import Header from '@containers/dashboard-header';
import './style.scss';

import LeftNavigation from "@containers/leftnavigation";
import FilterSection from '@containers/dashboard-filtersection';
import Overview from '@containers/dashboard-overview';
import Information from '@containers/dashboard-info';

const Docket = () => {
    return (
        <div className='dashboard-wrapper'>
            <LeftNavigation />
            <div className='mainarea'>
                <Header title={'Meadow v. UTC Aerospace systems'} />
                <div className='mainarea-sub1'>
                    <FilterSection />
                    <div className='mainarea-sub2'>
                        <Overview />
                        <Information />
                    </div>
                </div>
            </div>
        </div>
    )
}
export default Docket;