import './style.scss';
import {
    SearchOutlined,
    QuestionCircleOutlined
} from '@ant-design/icons';
interface PropType {
    title: string,
}
const Header = ({title} : PropType) => {
    return (
        <div className='dashboard-header'>
            <span className='dashboard-title'>{title}</span>
            <div className='header-menu-wrapper'>
                <SearchOutlined />
                <QuestionCircleOutlined />
                <div className='avatar-wrapper'>
                    <img src={require('@images/avatars/1.png')} className='img-avatar'/>
                    <span>Serati Ma</span>
                </div>
            </div>
        </div>
    )
}
export default Header;